import Vue from 'vue'
import App from './App.vue'
import Router from 'vue-router'
import Home from './views/home'
import Historia from './views/Historia'
import ProgramaExp from "./views/ProgramaExp";
import Variables from "@/views/Variables";
import Estructuras from "@/views/Estructuras";
import Expresiones from "@/views/Expresiones";
import Funciones from "@/views/Funciones";

Vue.config.productionTip = false
Vue.use(Router)
var router = new Router({
  routes:[
    {
    path:'/',
      name:'home',
    component: Home
    },
    {
      path:'/historia/',
      name: 'historia',
      component: Historia
    },
    {
      path:'/programaExp/',
      name: 'programa',
      component: ProgramaExp
    },
    {
      path:'/variables/',
      name:'variables',
      component: Variables
    },
    {
      path: '/estructuras/',
      name: 'estructuras',
      component: Estructuras
    },
    {
      path:'/expresiones',
      name: 'expresiones',
      component: Expresiones
    },
    {
      path: '/funciones',
      name: 'funciones',
      component: Funciones
    }
  ]
});

new Vue({
  router: router,
  render: h => h(App),
}).$mount('#app')
