<template>
    <section class="font-serif">
        <div class="body-style">
            <h1 class="title mt-12">Funciones</h1>
            <h2 class="subtitle mt-12">Definicion de funciones</h2>
            <p class="mt-8"> En Scala las definiciones de funciones empiezan con la palabra reservada <span class="font-bold">def</span> seguidas del identificador de la función, una lista de parámetros separados por comas y encerrados entre paréntesis.<br>
                Scala es un lenguage de tipado estático, así que debemos indicar el tipo de cada parámetro en una función después de su nombre y separado por :. Esto es debido a que Scala no infiere los tipos de los parámetros en las funciones.<br>
                Un ejemplo simple es una función que eleva al cuadrado un número entero. </p>
            <div class="code-style bg-yellow-200 code-style rounded-lg mt-8 text-center mt-8">
                <p><span class="font-bold">def</span> square(x: Int): Int = { <br>
                    x * x <br>
                    }</p>
            </div>
            <p class="mt-8">Hay ocasiones en que necesitamos declarar funciones que no devuelven ningún valor interesante, por ejemplo:</p>
            <div class="code-style bg-yellow-200 code-style rounded-lg mt-8 text-center mt-8" >
                <p >
                    <span class="font-bold">def</span> greet() = {<br>
                    println("Hello, world!")<br>
                    }
                </p>
            </div>
            <p class="mt-8">El tipo devuelto inferido por Scala en este ejemplo es <span class="font-bold">Unit</span>. El tipo Unit en Scala es similar al <span class="font-bold">void</span> de Java.</p>
            <h2 class="mt-8 subtitle">Funciones anónimas</h2>
            <p class="mt-8">Una función anónima, también llamada función literal o función <span class="font-bold">Lambda</span>, es una definición de una función que no está ligada a un identificador.<br>
                Scala tiene una sintaxis simple para definir funciones anónimas. La siguiente expresión crea una función sucesor anónima para números enteros:</p>
            <div class="code-style bg-yellow-200 code-style rounded-lg mt-8 text-center mt-8">
                <p>
                    (x: Int) <span class="font-bold">=></span> x + 1
                </p>
            </div>
            <p class="mt-8">El operador <span class="font-bold">=></span> indica que la función convierte el argumento que pasemos del lado izquierdo (cualquier número entero <span class="font-bold">x</span>) al resultado de evaluar la
                expresión que está del lado derecho  <span class="font-bold">( x + 1 ) </span>
                . Por lo tanto, esta función mapea cualquier entero <span class="font-bold">x</span> con <span class="font-bold">( x + 1 ) </span> (el sucesor de <span class="font-bold">x</span>).</p>
        </div>
    </section>
</template>

<script>
    export default {
        name: "Funciones"
    }
</script>

