<template>
    <section class="text-gray-800 font-serif ">
        <div class="body-style">
            <h1 class="title">Estructuras de control de flujo</h1>
            <div class="mt-12">
                <h2 class="subtitle">If / Else </h2>
                <p class="mt-8">La estructura de control if / else de Scala es similar a otros lenguajes: </p>
                <div class="code-style bg-yellow-200 rounded-lg mt-8">
                    <p><span class="font-bold">if </span>(test1) {</p>
                    <p>doA()</p>
                    <p>} <span class="font-bold">else if</span> (test2) {</p>
                    <p>doB()</p>
                    <p>} <span class="font-bold">else if</span> (test3) {</p>
                    <p>doC()</p>
                    <p>} <span class="font-bold">else</span> {</p>
                    <p>doD()</p>
                    <p>}</p>
                </div>
                <p class="mt-8">Sin embargo, a diferencia de Java y muchos otros lenguajes, la construcción if / else devuelve un valor, por lo que, entre otras cosas, puede usarlo como operador ternario:</p>
                <div class="code-style bg-yellow-200 rounded-lg mt-8">
                    <p>val x = <span class="font-bold">if</span> (a &gt; b) a <span class="font-bold">else</span> b</p>
                </div>
                <h2 class="subtitle mt-8">match expressions</h2>
                <p class="mt-8">Scala tiene una matchexpresión, que en su uso más básico es como una switchdeclaración de Java :</p>
                <div class="code-style bg-yellow-200 rounded-lg mt-8">
                 <p>val result = i <span class="font-bold">match</span> {</p>
                     <p><span class="font-bold">case</span> 1 => "one"</p>
                     <p><span class="font-bold">case</span> 2 => "two"</p>
                     <p><span class="font-bold">case</span> _ => "not 1 or 2"</p>
                     <p>}</p>
                </div>
                <p class="mt-8">La matchexpresión no se limita solo a enteros, se puede usar con cualquier tipo de datos, incluidos los booleanos. Aquí hay un ejemplo de matchser utilizado como el cuerpo de un método, y compararlo con muchos tipos diferentes:</p>
                <div class="code-style bg-yellow-200 rounded-lg mt-8 text-center">
                    <p>def getClassAsString(x: Any):String = x <span class="font-bold">match</span> {</p>
                        <p> <span class="font-bold">case</span> s: String => s + " is a String"</p>
                        <p> <span class="font-bold">case</span> i: Int => "Int"</p>
                       <p>  <span class="font-bold">case</span> f: Float => "Float"</p>
                       <p>  <span class="font-bold">case</span> l: List[_] => "List"</p>
                       <p>  <span class="font-bold">case</span> p: Person => "Person"</p>
                        <p> <span class="font-bold">case</span> _ => "Unknown"</p>
                      <p>  }</p>
                </div>
                <h2 class="subtitle mt-8">try/catch</h2>
                <p class="mt-8">La estructura de control try / catch de Scala le permite capturar excepciones. Es similar a Java, pero su sintaxis es consistente con las expresiones de coincidencia:</p>
                <div class="code-style bg-yellow-200 code-style rounded-lg mt-8 ">
                   <p><span class="font-bold">try</span> {</p>
                    <p>writeToFile(text)</p>
                    <p>} <span class="font-bold">catch</span> {</p>
                    <p><span class="font-bold">case</span> fnfe: FileNotFoundException => println(fnfe)</p>
                    <p><span class="font-bold">case</span> ioe: IOException => println(ioe)</p>
                    <p>}</p>
                </div>
                <h2 class="subtitle mt-8">for loops and expressions</h2>
                <p class="mt-8">Los forbucles Scala , que generalmente escribimos en este libro como bucles for, se ven así:</p>
                <div class="code-style bg-yellow-200 code-style rounded-lg mt-8 ">
                    <p><span class="font-bold">for</span> (arg &lt;- args) println(arg)</p>
                    <p><span class="font-bold">for</span> (i &lt;- 0 to 5) println(i)</p>
                    <p><span class="font-bold">for</span> (i &lt;- 0 to 10 by 2) println(i)</p>
                </div>
                <h2 class="subtitle mt-8">while y do / while</h2>
                <p class="mt-8">Scala también tiene whiley do/ whilebucles. Aquí está su sintaxis general:</p>
                <div class="code-style bg-yellow-200 code-style rounded-lg mt-8 ">
                    <p>// while loop</p>
                    <p><span class="font-bold">while</span>(condition) {</p>
                    <p>  statement(a)</p>
                    <p>statement(b)</p>
                    <p >}<p/><br class="my-2">
                    <p>// do-while</p>
                    <p>statement(a)</p>
                    <p>statement(b)</p>
                    <p>}</p>
                    <p>while(condition)</p>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
    export default {
        name: "Estructuras"
    }
</script>

